.home {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: #2a2a2a;
}
.home_context {
  position: absolute;
  height: 100vh;
  width: 100vw;
  left: 0;
  top: 0;
  margin: 0 auto;
  color: rgb(255, 255, 255);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  .title {
    color: white;
    display: inline-block;
    animation-direction: alternate;
    width: 22ch;
    font-size: 40px;
    font-family: monospace;
    white-space: nowrap;
    overflow: hidden;
    border-right: 4px solid orange;
    animation-fill-mode: forwards;
    animation: flashing-border 0.75s step-end infinite,
      printed-text 3s steps(22);
  }

  @keyframes flashing-border {
    0% {
      border-color: orange;
    }
    50% {
      border-color: transparent;
    }
    100% {
      border-color: orange;
    }
  }
  @keyframes printed-text {
    from {
      width: 0;
    }
  }

  .home_context_sub {
    opacity: 0.3;
    font-size: 24px;
  }

}
@media (max-width: 960px) {
  .home_context {
    width: calc(100vw - 150px);
    left: 150px;
  }
}
@media (max-width: 650px) {
  .home_context {
    width: 100vw;
    left: 0px;
    .title{
      font-size: 30px;
    }
    .home_context_sub{
      font-size: 20px;
    }
  }
}
@media (max-width: 400px) {
  .home_context {
    width: 100vw;
    left: 0px;
    .title{
      font-size: 25px;
    }
    .home_context_sub{
      font-size: 18px;
    }
  }
}