.contact{
  background-color: #2a2a2a;
  height: 100vh;
  width: 100vw;
  padding:  100px 60px 60px 300px;
  position: absolute;
  left: 0;
  &_form{
      height:100%;
      width: 600px;
      position: relative;
    form{
      width: 600px;
      height: 80%;
/*       display: flex;
      justify-content: space-around;
      align-items:center;
      flex-direction: column; */
      position: absolute;
      bottom: 0;
      left:0;
      z-index: 1;
      color: white;
      width: 600px;
      max-width: 600px;
      font-size: 20px;
      .error-email{
        color: red;
        transform: translateY(50px);
        position: absolute;
        top:45px;
      }

      input{
        outline: none;
        background-color: #2a2a2a;
        border: 2px solid rgba(255, 255, 255, 0.3);
        border-radius: 3px;
        width: 100%;
        height: 40px;
        color: white;
        margin: 30px 0;
        font-size: 18px;
        padding: 5px 20px;
        &:focus{
          border: 2px solid orange;
        }
        &:focus{
          background-color: #2a2a2a;
        }
      }
      textarea{
        outline: none;
        background-color: #2a2a2a;
        border: 2px solid rgba(255, 255, 255, 0.3);
        border-radius: 3px;
        width: 600px;
        max-width: 600px;
        max-height: 300px;
        min-height: 40px;
        min-width: 300px;
        height: 100px;
        color: white;
        margin: 5px 0;
        font-size: 18px;
        padding: 5px 20px;
        margin: 30px 0;

        &:focus{
          border: 2px solid orange;
        }
      }
      button{
        background: #2a2a2a;
        color: orange;
        border: 2px solid orange;
        border-radius: 3px;
        width: 300px;
        height: 40px;
        margin: 5px 0;
        font-size: 18px;
        padding: 5px 20px;
        cursor: pointer;
        &:disabled{
          opacity: .1;
        }
        &:hover{
        color: #2a2a2a;
        background:  orange;
        }
      }
    }
  }
  .title {
    position: absolute;
    color: white;
    display: inline-block;
    animation-direction: alternate;
    width: 8ch;
    font-size: 40px;
    font-family: monospace;
    white-space: nowrap;
    overflow: hidden;
    border-right: 4px solid orange;
    animation-fill-mode:forwards;
    animation: flashing-border .75s step-end infinite,
              printed-text 3s steps(8),
  }

  @keyframes flashing-border {
    0% {
      border-color: orange;
    }
    50% {
      border-color: transparent;
    }
    100% {
      border-color: orange;
    }
  }
  @keyframes printed-text {
    from{
      width: 0;      
    }
  }
  .mail-svg{
    position: absolute;
    bottom: -45px;
    right: 200px;
    .animSvg{
      stroke-dasharray:178 ;
      stroke-dashoffset: 178;
      animation: mailBox 3s ease-in-out 1s 1;
      animation-fill-mode:forwards;
      fill:none;
      opacity: .6;
      // fill-opacity: 0;
    }
    @keyframes mailBox {
      90%{
      // fill-opacity: 0;
      }
      100%{
      stroke-dashoffset: 0;
      stroke-dasharray:178;
      // fill-opacity: 1;
      fill: none;
      }
    }
  }
}
@media(max-width:1300px){
  .contact .mail-svg{
    right: 50px
  }
}
@media(max-width:1300px){
  .contact .mail-svg{
    right: 50px;
    #mailBox{
      width: 250px;height: 250px;
    }
  }
} 
@media(max-width:930px){
  .contact{
    padding: 100px 60px 60px 150px;
    .contact_form{
      width: 100%;
      form{
        width: 100%;
        textarea{
          width: 100%;
        }
      }
    }
  }
}

@media(max-width:650px){
  
  .contact{
    padding: 100px 20px 60px 20px;

  }
}
