.profile{
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-color:#2a2a2a;
  .profile_title {
/*     color: white;
    position: absolute;
    top: 100px;
    left: 360px;
    display: inline-block;
    margin-top: 30px;
    width: 9ch;
    font-size: 40px;
    overflow: hidden;
    font-family: monospace;
    white-space: nowrap;
    border-right: 4px solid orange;
    animation: flashing-border 0.75s step-end infinite, printed-text 3s steps(9);
    animation-direction: alternate;
    animation-fill-mode: forwards; */
    visibility: hidden;
    opacity: 0;
  }
  .profile_title-active {
    color: white;
    position: absolute;
    top: 100px;

    left: 360px;
    display: inline-block;
    margin-top: 30px;
    width: 8ch;
    font-size: 40px;

    font-family: monospace;
    white-space: nowrap;
    border-right: 4px solid orange;
    animation: flashing-border 0.75s step-end infinite, printed-text 1s steps(8);
    animation-direction: alternate;
    animation-fill-mode: forwards;
    visibility: visible;
    opacity: 1;
  }
  .profile_info{
    color: white;
    font-size: 22px;
    top: 60px;
    left: 360px;
    width: 40vw;
    min-height: 300px;
    position: absolute;
    transition: .2s;
    opacity: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    visibility: hidden;

  }
  .profile_info-active{
    color: white;
    font-size: 22px;
    left: 360px;
    top: 300px;
    width: 40vw;
    min-height: 300px;
    position: absolute;
    transition: .2s;
    opacity: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    .icon_chart{
      opacity: 0.6;
      display: flex;
      justify-content: center;
      margin-top: 40px;
      padding-bottom: 40px;
      transition: .1s;
      width: 50px;
      cursor: pointer;
      &:hover{
        opacity: 1;
      }
    }
    .animSvg{
      stroke-dasharray:178 ;
      stroke-dashoffset: 178;
      animation: animIcons 2s ease-in-out 0.5s 1;
      animation-fill-mode:forwards;
      fill:orange;
      fill-opacity: 0;
    }
    @keyframes animIcons {
      90%{
      fill-opacity: 0;
      }
      100%{
      stroke-dashoffset: 0;
      stroke-dasharray:178;
      fill-opacity: 1;
      fill: orange;
      }
    }
  }
  .charts{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: calc(100vh - 90px);
  }
  .chartBar{
    width: 40vw;
    height: 30vh;
    z-index: 1;
    opacity: 0;
    visibility: hidden;
    transition: .2s;
    // transition-delay: .1s;
  }
  .chartBar-active{
    width: 40vw;
    height: 30vh;
    z-index: 1;
    opacity: 1;
    visibility: visible;
    transition: .2s;
    // transition-delay: .1s;
  }
  .chartBar_horizontal{
    width: 40vw;
    height: 30vh;
    z-index: 1;
    opacity: 0;
    visibility: hidden;
    transition: .2s;
  }  .chartBar_horizontal-active{
    width: 40vw;
    height: 30vh;
    z-index: 1;
    opacity: 1;
    visibility: visible;
    transition: .2s;
  }
}


@keyframes flashing-border {
  0% {
    border-color: orange;
  }
  50% {
    border-color: transparent;
  }
  100% {
    border-color: orange;
  }
}
@keyframes printed-text {
  from{
    width: 0;      
  }
}


@media(max-width: 1000px){

  #profile_info.profile_info-active{
    left: 150px;
    width: 70vw;
  }
  #profile_title.profile_title-active{
    left: 150px;
  }
 
}
@media(max-width: 650px){
  
   #profile_info.profile_info-active{
     left: 0px;
     width: 100vw;
     padding: 0 50px;
   }
   #profile_title.profile_title-active{
     left: 50px;
   }
  
 }

 @media(max-width:500px){
  #profile_info.profile_info-active{
    width: 100%;
    height: 50vh;
    padding: 15px;
  }
  #profile_title.profile_title-active{
    padding: 0px;
    left: 15px;
  }
}



















