.socials{
  width: 300px;
  height: 150px;
  display: flex;
  align-items: flex-end;
  justify-content: space-evenly;
  svg{
    cursor: pointer;
    opacity: 0.6;
    transition: .1s;
    &:hover{
      opacity: 1;
    }}
  .animSvg{
    stroke-dasharray:178 ;
    stroke-dashoffset: 178;
    animation: animIcons 3s ease-in-out 2s 1;
    animation-fill-mode:forwards;
    fill:orange;
    fill-opacity: 0;


  }
  @keyframes animIcons {
    90%{
    fill-opacity: 0;
    }
    100%{
    stroke-dashoffset: 0;
    stroke-dasharray:178;
    fill-opacity: 1;
    fill: orange;
    }
  }
}