.case {
  background-color: #2a2a2a;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 5vw;
  right: 0;
  bottom: 0;
  .slider-case {
    width: 100vw;
    height: 100vh;
    position: absolute;
    padding: 100px 60px 60px 300px;

    .slide {
      height: 100%;
      padding: 0 60px;
      .title {
        margin: 30px 0 0 0;
        left: 30vw;
        color: white;
        display: inline-block;
        animation-direction: alternate;
        width: 13ch;
        font-size: 40px;
        font-family: monospace;
        white-space: nowrap;
        overflow: hidden;
        border-right: 4px solid orange;
        animation-fill-mode: forwards;
        animation: flashing-border 0.75s step-end infinite,
          printed-text 3s steps(13);
      }

      @keyframes flashing-border {
        0% {
          border-color: orange;
        }
        50% {
          border-color: transparent;
        }
        100% {
          border-color: orange;
        }
      }
      @keyframes printed-text {
        from {
          width: 0;
        }
      }
      .certificates {
        display: flex;
        height: 90%;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        padding: 20px 20px;
        .cert {
          color: white;
          display: flex;
          justify-content: space-between;
          width: 100%;

          p {
            width: 40vw;
            font-size: 30px;
            span {
              font-size: 18px;
            }
          }

          a {
            img {
              transition: 0.2s;
              color: white;
              width: 402px;
              height: 252px;
              border-radius: 10px;
              border: 1px solid white;
              position: relative;
              overflow: hidden;
              margin: 30px 30px 30px 0;
              padding: 15px;
              &:hover {
                border: 1px solid orange;
              }
            }
          }
        }
      }
    }
    .examples {
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-wrap: wrap;
      padding: 50px 0;
      /*       display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(2, 1fr);
      grid-column-gap: 40px;
      grid-row-gap: 50px;
      justify-items: end;
      align-items: center; */
      .card {
        width: 502px;
        height: 302px;
        border-radius: 10px;
        border: 1px solid white;
        background-image: url("../sources/vid.mp4");
        background-position: center;
        background-size: contain;
        position: relative;
        margin: 30px 0;

        &:hover {
          border: 1px solid orange;
        }
        &:hover::before {
          content: "";
          display: block;
          position: absolute;
          z-index: 1;
          border: 2px solid transparent;
          width: 40px;
          height: 40px;
          left: calc(50%);
          top: calc(50%);
          transform: translate(-50%, -50%);
          animation: video-focus-1 0.2s ease forwards;
        }
        &:hover::after {
          content: "";
          display: block;
          position: absolute;
          z-index: 1;
          border: 2px solid transparent;
          width: 40px;
          height: 40px;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          animation: video-focus-2 0.2s ease forwards;
        }
        @keyframes video-focus-1 {
          0% {
            width: 0;
            height: 0;
            border-top-color: orange;
            border-right-color: transparent;
            border-bottom-color: transparent;
            border-left-color: transparent;
          }
          50% {
            width: 20px;
            height: 0;
            border-top-color: orange;
            border-right-color: orange;
            border-bottom-color: transparent;
            border-left-color: transparent;
          }
          100% {
            width: 20px;
            height: 20px;
            border-top-color: orange;
            border-right-color: orange;
            border-bottom-color: transparent;
            border-left-color: transparent;
          }
        }
        @keyframes video-focus-2 {
          0% {
            width: 0;
            height: 0;
            border-top-color: transparent;
            border-right-color: transparent;
            border-bottom-color: transparent;
            border-left-color: orange;
          }
          50% {
            width: 0;
            height: 20px;
            border-top-color: transparent;
            border-right-color: transparent;
            border-bottom-color: orange;
            border-left-color: orange;
          }
          100% {
            width: 20px;
            height: 20px;
            border-top-color: transparent;
            border-right-color: transparent;
            border-bottom-color: orange;
            border-left-color: orange;
          }
        }
        .card_link {
          position: absolute;
          left: 50%;
          bottom: 10%;
          transform: translate(-50%, -50%);
          text-decoration: none;
          color: orange;
          font-size: 30px;
          z-index: 2;
          opacity: 0;
        }
        video {
          transition: 0.2s;
        }
        video:hover {
          filter: grayscale(100%) blur(5px);
        }
      }
    }
    .slick-arrow {
      display: block;
      position: relative;
      right: 0px;
      top: 0;
      z-index: 0;
    }
    .slick-prev {
      position: absolute;
      top: 55px;
      left: calc(90% - 50px);
      z-index: 1;

      &::before {
        content: url("../sources/arrow-left.svg");
        transform: scale(2);
        line-height: 1;
        top: 0;
        left: 0;
        opacity: 0.75;
        position: absolute;
        transition: 0.2s;
      }
      &:hover::before {
        opacity: 1;
      }
    }
    .slick-next {
      position: absolute;
      top: 55px;
      left: calc(90% + 50px);
      &::before {
        content: url("../sources/arrow-right.svg");
        transform: scale(2);
        line-height: 1;
        top: 0;
        left: 0;
        opacity: 0.75;
        position: absolute;
        transition: 0.2s;
      }
      &:hover::before {
        opacity: 1;
      }
    }
  }
}

@media(max-width:1500px){
  .case .slider-case .examples .card{
    width: 404px;
    height: 237px;

    video{
      width: 400px;
    height: 235px;
      
  }
  } 
}

@media(max-width:1300px){
  .case{
    .slider-case{
      padding: 100px 60px 60px 150px;
      .slide{
        padding: 0 20px 0 0;
      }
    }
  }
}

@media(max-width:1050px){
  .case .slider-case .examples {
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 50px 0;
}
}

@media(max-width:960px){
  .case .slider-case .slide{
    height: calc(100% - 100px);
    .cert {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 100%;
    }
  }
  .case .slider-case .slide .certificates .cert p {
    width: 100%;

  } 
}

@media(max-width:650px){
  .case .slider-case{
    padding: 100px 15px 10px 15px;
  }
  .case .slider-case .slide{
  }
  .case .slider-case .slide .title{
    margin:30px 0;
  }
  .case .slider-case .slide .certificates{
    padding: 0px 20px;
    .cert{
      align-items: center;
  }
  } 
  .case .slider-case .slide .certificates .cert a img{
    width: 302px;
    height: 202px;
    margin: 0;
  }
  .case .slider-case .slick-prev {
    position: absolute;
    top: -40px;
    left: 50%;
    transform: translate(-200%);
  }
  .case .slider-case .slick-next {
    position: absolute;
    top: -40px;
    left: 50%;
    transform: translate(200%);
  }
}
@media(max-width: 460px){
  .case .slider-case .examples .card{
    width: 292px;
    height: 162px;
    video{
      width: 290px;
      height: 160px;
    }
  } 
}
@media(max-width:400px){
  .case .slider-case .slide .certificates .cert a img{
    width: 282px;
    height: 182px;
    margin: 0;
  }
  .case .slider-case .slide .title{
    margin-bottom:  30px;

  }
}